<template>
  <div>
    <van-nav-bar
      :fixed="true"
      :title="name"
      @click-left="()=>{
          $router.go(-1)
         }"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="filter-o" slot="right" size="20"  /> -->
    </van-nav-bar>
    <div class="flexTitle backgroundNaviBar" style="z-index: 999;">
      <div class="rate" @click="rateBtnNew()" :style="colorStyle">超标</div>
      <div class="noRate" @click="normalBtnNew()" :style="colorStyleNo">达标</div>
    </div>
    <div class="titleFactor">
      <div style="width:200px;">时间</div>
      <div>因子名称</div>
      <div>监测值</div>
      <!-- <span v-if="$route.query.name!=='ph'">mg/L</span> -->
    </div>
    <div class="containerContentFactor">
      <div class="backContent">
        <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
          <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
            <!-- <van-cell v-for="item in list" :key="item" :title="item" /> -->
            <div
              class="flexBox borderStyle"
              v-for="(item,index) in itemContentList"
              :key="index"
              :title="item"
            >
              <div style="width:200px;">{{item.time}}</div>
              <div>{{item.name}}</div>
              <!-- <div
            :style="item.factor===null?'':'color:'+item.color"
              >{{item.factor===null?'--':item.name==='pH'?item.factor:item.factor+'mg/l'}}</div>-->
              <div :style="'color:'+item.color" v-show="item.name==='pH'">{{item.factor}}</div>
              <div :style="'color:'+item.color" v-show="item.name!=='pH'">{{item.factor|filterCell}}</div>
            </div>
            <div class="flexBoxNew" v-show="showMore==true">
              <!-- <span>没有更多了</span> -->
            </div>
          </van-list>
        </van-pull-refresh>
      </div>
    </div>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
import Vue from "vue";
import {
  Skeleton,
  ImagePreview,
  Collapse,
  CollapseItem,
  PullRefresh
} from "vant";
import { List } from "vant";

Vue.use(List).use(PullRefresh);
export default {
  data() {
    return {
      itemContentList: [],
      token: "",
      name: "",
      //   超标
      rateData: [],
      normalData: [],
      nameFactor: "",
      colorStyle: "color:white",
      colorStyleNo: "color:#CCC;border:none",
      rateStatus: true,
      normalStatus: true,
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      num: 1,
      buttonTrue: false,
      pageSize: 20,
      dataMapLength: 40,
      showMore:false
    };
  },
  filters:{
    filterCell(value){
      if (value !== null) {
        if (typeof value == "number") {
          return value.toFixed(3)+"mg/L";
        } else {
          return value+"mg/L";
        }
      } else {
        return "--";
      }
    }
  },
  methods: {
    rateBtnNew() {
      this.colorStyle = "color:white";
      this.colorStyleNo = "color:#CCC;border:none";
      this.num = 1;
      this.initData();
    },
    normalBtnNew() {
      this.num = 0;
      this.colorStyleNo = "color:white";
      this.colorStyle = "color:#CCC;border:none";
      this.initData();
    },
    onLoad() {
      // setTimeout(() => {
      if (this.refreshing) {
        this.itemContentList = [];
        this.refreshing = false;
        this.pageSize = 20;
      }

      // for (let i = 0; i < 10; i++) {
      //   this.list.push(this.list.length + 1);
      // }
      this.pageSize = this.pageSize + 10;
      this.loading = false;
      this.initData();

      if (this.itemContentList.length >= this.dataMapLength) {
        this.finished = true;
        // this.pageSize = 20;
      }
      // }, 000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
    async initData() {
      // var url = "/station/water-data/form";
      var url = "/station/water-data/exceedAndStandard";
      // var url = ""
      //  参数：starttime/waterstation/lasttime
      // console.log()
      // console.log(this.waterstation);
      //   console.log(this.$store.state.menu);
      // var waterstation = this.forMenuName(this.$route.query.station);
      var waterstation = this.$route.query.station;
      this.name = waterstation;
      var starttime = this.$route.query.startTime;
      var lasttime = this.$route.query.endTime;
      var specifyFactor = this.$route.query.name;
      var isExceeds = this.num;
      // debugger
      var isExcel = "no";
      // debugger
      var pageNum = 1;
      var pageSize = this.pageSize;
      var res = await AppBace.request(
        url,
        "POST",
        {
          starttime,
          lasttime,
          waterstation,
          isExcel,
          pageNum,
          pageSize,
          specifyFactor,
          isExceeds
        },
        this.token
      );
      console.log("11111111111111", res);
      if (res.status === 200) {
        this.rateData = [];
        this.normalData = [];
        //    对数据进行处理
        var dataname = "";
        if (isExceeds == 0) {
          dataname = "upStandard";
        } else {
          dataname = "overStandard";
        }
        var resArr = res.data.data.dataMap[dataname];
        this.dataMapLength = res.data.data.dataMap.total;
        // debugger
        // console.log(res.data.data.dataMap[dataname]);
        switch (this.$route.query.name) {
          case "ph":
            this.handleData(this.$route.query.name, resArr);
            break;
          // 化学含氧量
          case "cod":
            this.handleDataCod(this.$route.query.name, resArr);
            break;
          // 氨氮
          case "ammoniaNitrogen":
            this.handleDataAmmoniaNitrogen(this.$route.query.name, resArr);
            break;
          // 总氮
          case "nitrogen":
            this.handleDataNitrogen(this.$route.query.name, resArr);
            break;
          // 总磷
          case "phosphorus":
            this.handleDataPhosphorus(this.$route.query.name, resArr);
            break;
          // 总铜
          case "cuprum":
            this.handleDataCuprum(this.$route.query.name, resArr);
            break;
          // 总镍
          case "nickel":
            this.handleDataNickel(this.$route.query.name, resArr);
            break;
          // 总铬
          case "chromium":
            this.handleDataChromium(this.$route.query.name, resArr);
            break;
          // 总氰化物
          case "prussiate":
            this.handleDataPrussiate(this.$route.query.name, resArr);
            break;
          default:
            break;
        }

        // this.itemContentList = res.data.data;
        this.itemContentList = this.rateData.concat(this.normalData);
        
        if (this.itemContentList.length==0) {
          this.finished = true;
        }else{
          // this.finished = false;
        }
        // this.itemContentList.sort(function(a, b) {
        //   return a.time < b.time ? 1 : -1;
        // });
      }
    },
    forMenuName(name) {
      var array = this.$store.state.menu;
      for (let index = 0; index < array.length; index++) {
        var nameStation = array[index];
        if (nameStation.slice(0, 4) === name) {
          name = array[index];
          this.name = name;
          return name;
        }
      }
    },
    handleData(name, res) {
      this.nameFactor = "pH";
      var phArr = [];
      var phArrRate = [];
      for (let index = 0; index < res.length; index++) {
        var ph = res[index].ph;
        // 超标的因子
        if (
          ph < this.$store.state.factor.ph ||
          ph > this.$store.state.factor.ph1
        ) {
          // 超标
          phArrRate.push({
            factor: ph,
            name: "pH",
            time: res[index].gmtMonitor,
            color: "red"
          });
        } else {
          // 达标
          phArr.push({
            factor: ph,
            name: "pH",
            time: res[index].gmtMonitor,
            color: ""
          });
        }
      }
      this.rateData = phArrRate;
      this.normalData = phArr;
    },
    // COD
    handleDataCod(name, res) {
      var codArr = [];
      var codArrRate = [];
      for (let index = 0; index < res.length; index++) {
        var cod = res[index].cod;
        // 超标的因子
        if (cod > this.$store.state.factor.cod * 1.15) {
          // 超标
          codArrRate.push({
            factor: cod,
            name: "COD",
            time: res[index].gmtMonitor,
            color: "red"
          });
        } else if (
          cod < this.$store.state.factor.cod * 1.15 &&
          cod > this.$store.state.factor.cod
        ) {
          // 超标
          codArrRate.push({
            factor: cod,
            name: "COD",
            time: res[index].gmtMonitor,
            color: "orange"
          });
        } else {
          // 达标
          codArr.push({
            factor: cod,
            name: "COD",
            time: res[index].gmtMonitor,
            color: ""
          });
        }
      }
      this.rateData = codArrRate;
      this.normalData = codArr;

      this.itemContentList = this.rateData;
    },
    // 氨氮
    handleDataAmmoniaNitrogen(name, res) {
      var ammoniaNitrogenArr = [];
      var ammoniaNitrogenArrRate = [];
      for (let index = 0; index < res.length; index++) {
        var ammoniaNitrogen = res[index].ammoniaNitrogen;
        // 超标的因子
        if (ammoniaNitrogen > this.$store.state.factor.ammoniaNitrogen * 1.15) {
          // 超标
          ammoniaNitrogenArrRate.push({
            factor: ammoniaNitrogen,
            name: "氨氮",
            time: res[index].gmtMonitor,
            color: "red"
          });
        } else if (
          ammoniaNitrogen < this.$store.state.factor.ammoniaNitrogen * 1.15 &&
          ammoniaNitrogen > this.$store.state.factor.ammoniaNitrogen
        ) {
          // 超标
          ammoniaNitrogenArrRate.push({
            factor: ammoniaNitrogen,
            name: "氨氮",
            time: res[index].gmtMonitor,
            color: "orange"
          });
        } else {
          // 达标
          ammoniaNitrogenArr.push({
            factor: ammoniaNitrogen,
            name: "氨氮",
            time: res[index].gmtMonitor,
            color: ""
          });
        }
      }
      this.rateData = ammoniaNitrogenArrRate;
      this.normalData = ammoniaNitrogenArr;
    },
    // 总氮
    handleDataNitrogen(name, res) {
      var nitrogenArr = [];
      var nitrogenArrRate = [];
      for (let index = 0; index < res.length; index++) {
        var nitrogen = res[index].nitrogen;
        // 超标的因子
        if (
          nitrogen > this.$store.state.factor.nitrogen &&
          nitrogen < this.$store.state.factor.nitrogen * 1.15
        ) {
          // 超标
          nitrogenArrRate.push({
            factor: nitrogen,
            name: "总氮",
            time: res[index].gmtMonitor,
            color: "orange"
          });
        } else if (nitrogen > this.$store.state.factor.nitrogen * 1.15) {
          // 超标
          nitrogenArrRate.push({
            factor: nitrogen,
            name: "总氮",
            time: res[index].gmtMonitor,
            color: "red"
          });
        } else {
          // 达标
          nitrogenArr.push({
            factor: nitrogen,
            name: "总氮",
            time: res[index].gmtMonitor,
            color: ""
          });
        }
      }
      this.rateData = nitrogenArrRate;
      this.normalData = nitrogenArr;
    },
    //总磷
    handleDataPhosphorus(name, res) {
      var phosphorusArr = [];
      var phosphorusArrRate = [];
      for (let index = 0; index < res.length; index++) {
        var phosphorus = res[index].phosphorus;
        // 超标的因子
        if (
          phosphorus > this.$store.state.factor.phosphorus &&
          phosphorus < this.$store.state.factor.phosphorus * 1.15
        ) {
          // 超标
          phosphorusArrRate.push({
            factor: phosphorus,
            name: "总磷",
            time: res[index].gmtMonitor,
            color: "orange"
          });
        } else if (phosphorus > this.$store.state.factor.phosphorus * 1.15) {
          // 超标
          phosphorusArrRate.push({
            factor: phosphorus,
            name: "总磷",
            time: res[index].gmtMonitor,
            color: "red"
          });
        } else {
          // 达标
          phosphorusArr.push({
            factor: phosphorus,
            name: "总磷",
            time: res[index].gmtMonitor,
            color: ""
          });
        }
      }
      this.rateData = phosphorusArrRate;
      this.normalData = phosphorusArr;
    },
    // 总铜
    handleDataCuprum(name, res) {
      var cuprumArr = [];
      var cuprumArrRate = [];
      for (let index = 0; index < res.length; index++) {
        var cuprum = res[index].cuprum;
        // 超标的因子
        if (cuprum > this.$store.state.factor.cuprum) {
          // 超标
          cuprumArrRate.push({
            factor: cuprum,
            name: "总铜",
            time: res[index].gmtMonitor,
            color: "red"
          });
        } else {
          // 达标
          cuprumArr.push({
            factor: cuprum,
            name: "总铜",
            time: res[index].gmtMonitor,
            color: ""
          });
        }
      }
      this.rateData = cuprumArrRate;
      this.normalData = cuprumArr;
    },
    // 总镍
    handleDataNickel(name, res) {
      var nickelArr = [];
      var nickelArrRate = [];
      for (let index = 0; index < res.length; index++) {
        var nickel = res[index].nickel;
        // 超标的因子
        if (nickel > this.$store.state.factor.nickel) {
          // 超标
          nickelArrRate.push({
            factor: nickel,
            name: "总镍",
            time: res[index].gmtMonitor,
            color: "red"
          });
        } else {
          // 达标
          nickelArr.push({
            factor: nickel,
            name: "总镍",
            time: res[index].gmtMonitor,
            color: ""
          });
        }
      }
      this.rateData = nickelArrRate;
      this.normalData = nickelArr;
    },
    // 总铬
    handleDataChromium(name, res) {
      var chromiumArr = [];
      var chromiumArrRate = [];
      for (let index = 0; index < res.length; index++) {
        var chromium = res[index].chromium;
        // 超标的因子
        if (
          chromium > this.$store.state.factor.chromium &&
          chromium < this.$store.state.factor.chromium * 1.15
        ) {
          // 超标
          chromiumArrRate.push({
            factor: chromium,
            name: "总铬",
            time: res[index].gmtMonitor,
            color: "orange"
          });
        } else if (chromium > this.$store.state.factor.chromium * 1.15) {
          chromiumArrRate.push({
            factor: chromium,
            name: "总铬",
            time: res[index].gmtMonitor,
            color: "red"
          });
        } else {
          // 达标
          chromiumArr.push({
            factor: chromium,
            name: "总铬",
            time: res[index].gmtMonitor,
            color: ""
          });
        }
      }
      this.rateData = chromiumArrRate;
      this.normalData = chromiumArr;
    },
    // 总氰化物
    handleDataPrussiate(name, res) {
      var prussiateArr = [];
      var prussiateArrRate = [];
      for (let index = 0; index < res.length; index++) {
        var prussiate = res[index].prussiate;
        // 超标的因子
        if (prussiate > this.$store.state.factor.prussiate) {
          // 超标
          prussiateArrRate.push({
            factor: prussiate,
            name: "总氰化物",
            time: res[index].gmtMonitor,
            color: "red"
          });
        } else {
          // 达标
          prussiateArr.push({
            factor: prussiate,
            name: "总氰化物",
            time: res[index].gmtMonitor,
            color: ""
          });
        }
      }
      this.rateData = prussiateArrRate;
      this.normalData = prussiateArr;
    },
    // 超标
    rateBtn() {
      //   this.itemContentList = this.rateData.concat(this.normalData);
      this.rateStatus = !this.rateStatus;
      if (this.rateStatus) {
        this.colorStyle = "";
        // this.colorstyleNo = "border:none;color:#ccc;";
        if (this.normalStatus === true) {
          this.itemContentList = this.rateData.concat(this.normalData);
        } else {
          this.itemContentList = this.rateData;
        }

        // this.rateStatus = false;
      } else {
        this.colorStyle = "border:none;color: #ccc;";
        if (this.normalStatus === true) {
          this.itemContentList = this.normalData;
        } else {
          this.itemContentList = this.rateData;
        }
        // this.itemContentList = this.normalData;
      }
      //   this.rateStatus = this.rateStatus;
      this.itemContentList.sort(function(a, b) {
        return a.time < b.time ? 1 : -1;
      });
    },
    normalBtn() {
      //   this.rateStatus = !this.rateStatus;
      this.normalStatus = !this.normalStatus;
      if (this.normalStatus) {
        this.colorStyleNo = "";
        if (this.rateStatus === true) {
          this.itemContentList = this.rateData.concat(this.normalData);
          //   this.itemContentList = this.rateData;
        } else {
          this.itemContentList = this.normalData;
        }
        // this.itemContentList = this.rateData;
      } else {
        this.colorStyleNo = "border:none;color:#ccc";
        if (this.rateStatus === true) {
          this.itemContentList = this.rateData;
        } else {
          this.itemContentList = [];
        }
        // this.itemContentList = this.rateData;
      }
      //   this.normalStatus = !this.normalStatus;
      this.itemContentList.sort(function(a, b) {
        return a.time < b.time ? 1 : -1;
      });
    }
  },
  mounted() {
    this.token = sessionStorage.getItem("token");
    this.initData();
    
  }
};
</script>
<style scoped>
.containerContentFactor {
  margin-top: 136px;
  /* padding: 10px; */
}
.header {
  position: fixed;
  top: 0;
  width: 100%;
}
.titleFactor {
  display: flex;
  flex-direction: row;
  height: 40px;
  justify-content: center;
  align-items: center;
  border-bottom: 1px rgb(236, 236, 236) solid;
  position: fixed;
  top: 96px;
  width: 100%;
  z-index: 999;
  background-color: white;
  /* position: fixed; */
}
.titleFactor div {
  width: 33.33%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.backContent {
  background-color: white;
  /* padding: 5px 0; */
  border-radius: 10px;
}
.contentTable {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  border-bottom: 1px rgb(247, 247, 247) solid;
}
.contentTable div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 33.33%;
  height: 40px;
}
.flexTitle {
  position: fixed;
  top: 46px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  height: 50px;
  background-color: white;
  font-size: 12px;
}
.flexTitle div {
  width: 50px;
  text-align: center;
  color: #3572fe;
  padding: 2px;
}
.rate {
  border: 2px solid white;
  color: white;
  border-radius: 10px;
}
.noRate {
  border: 2px solid white;
  color: white;
  border-radius: 10px;
}
.flexBox {
  display: flex;
  /* justify-content: space-between; */
  flex-direction: row;
  height: 40px;
}
.flexBox div {
  width: 33.33%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.flexBoxNew{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  height: 50px;
  color: grey;
}
</style>


